'use strict';

// MODULES //

import isnan from '@stdlib/math-base-assert-is-nan';


// VARIABLES //

var KURTOSIS = 0.8691773036059736; // 8*(π-3) / (π-2)²


// MAIN //

/**
* Returns the excess kurtosis of a half-normal distribution.
*
* @param {PositiveNumber} sigma - scale parameter
* @returns {number} excess kurtosis
*
* @example
* var y = kurtosis( 1.0 );
* // returns ~0.869
*
* @example
* var y = kurtosis( 4.0 );
* // returns ~0.869
*
* @example
* var y = kurtosis( NaN );
* // returns NaN
*
* @example
* var y = kurtosis( 0.0 );
* // returns NaN
*/
function kurtosis( sigma ) {
	if ( isnan( sigma ) || sigma <= 0.0 ) {
		return NaN;
	}
	return KURTOSIS;
}


// EXPORTS //

export default kurtosis;
